* {
  /* font-weight: bold; */
  padding: 0;
  margin: 0;
}

:root {
/******** COLORS *******/
/* Body */
--color-white: #EDEDED;
/* theme */
--color-theme-dark:#104462;
--color-theme-dark2:#053654;
--color-secondary-gray:#999;
--color-light-gray:#eee;
/* buttons */
--color-disabled-border: #dddddd;
--color-disabled-button: #469;
--color-disabled: #eee;
--color-delete: rgb(170, 12, 12);


/* font colors */
--color-font-white: #fff;
/******** SIZES *******/

--font-size-header-lg: 50px;
--font-size-header-md: 40px;
--font-size-header-sm: 30px;
--font-size-header-xs: 20px;
--font-size-header-mobile: 15px;
--font-size-large: 30px;
--font-size-text-s: 16px;
--font-size-text-xs: 12px;

/* Fonts */
--font-family-fm: 'Oxanium', sans-serif;

}

body {
    font-family: var(--font-family-fm) !important;
    font-family: 'Oxanium', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  
  .MuiTypography-root.label {
    font-size: large;
    color: var(--color-theme-dark);
    font-weight: 600;
  }
  
  html, body {
    height: 100%;
    margin: 0;
    font-family: 'Oxanium', sans-serif;
}